import React from 'react';
import './PlayList.css';

import TrackList from '../TrackList/TrackList';

class PlayList extends React.Component {
  constructor(props){
    super(props);
    this.handleNameChange = this.handleNameChange.bind(this);
  }

  /*the method should accept an event that is triggered by an onChange attribute in the PlayList components <input> element. Inside the method call .onNameChange() with the event target's value (from the <input> element)*/

  handleNameChange(e){
    this.props.onNameChange(e.target.value);
  }

  render(){
    return (
      <div className="Playlist">
        <input defaultValue={'New Playlist'} onChange={this.handleNameChange}/>

        {/*<!-- Add a TrackList component -->*/}
        {/*pass the playList tracks from the PlayList component to the TrackList component*/}
        {/*pass the onRemove from the playList component to the tracklist component, pass isRemoval with a value of true down to tracklist*/}
        <TrackList tracks={this.props.playListTracks} onRemove={this.props.onRemove} isRemoval={true}/>

        {/*these things are called elements*/}

        <a className="Playlist-save" onClick={this.props.onSave}>SAVE TO SPOTIFY</a>
      </div>
    )
  }
};

export default PlayList;



